@tailwind base;
@tailwind components;
@tailwind utilities;

html,
body {
  margin: 0 auto;
  max-width: 1580px;

  font-family: 'Quicksand', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-color: #3d9970;
  overflow-x: hidden;
}
p {
  font-size: 22px;
}

.button {
  margin: 0 0.25rem;
  border: none;
  background: white;
  padding: 0.25rem;
  font-size: 0.875rem;
  color: #007c6c;
  font-weight: bold;
}

.selected-image {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
}

.selected-image__wrapper {
  position: relative;
  margin: 1rem auto 0;
}

.selected-image__wrapper img {
  position: relative;
  width: 100%;
}

.results__container {
  margin: 0 auto;
}

.selected-image__processing {
  flex-basis: 100%;
}

.selected-image__image {
  position: relative;
}

.selected-image__overlay {
  position: absolute;
  left: 0;
  top: 0;
}

@media only screen and (min-width: 768px) {
  .selected-image {
    padding-top: 4px;
  }

  .selected-image__wrapper,
  .selected-image .results__container {
    width: 50%;
    margin: 0;
  }

  .results__container p {
    padding: 0 1rem;
  }
}

.results p {
  font-size: 1.25rem;
}
.emoj{
  height: 100px;
}
.results__wrapper {
  display: flex;
  margin-top: 1rem;
  color: black;
}

.results__emoji {
  color: #3d9970;
}

.results__text {
  width: 300px;
  color: black;
}

.results__emoji svg {
  margin-right: 0.75rem;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.gallery__container {
  margin-top: 2rem;
  width: 100%;
  min-height: 500px;
}

.gallery__container h2 {
  text-align: center;
}

.gallery__image-wrapper {
  position: relative;
  margin: 1rem;
  width: 150px;
  height: 120px;
}

.gallery__photo {
  width: 100%;
  opacity: 0.5;
  cursor: pointer;
  border: 1px solid #fff;
  border-radius: 2px;
}

.gallery__photo-delete {
  position: absolute;
  top: 4px;
  left: 4px;
  color: rgba(255, 109, 109, 0.762);
  cursor: pointer;
}

.gallery__photo--selected {
  opacity: 1;
}

.camera {
  width: 100%;
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  border-radius: 100px;
}
p.scroll_down
{
	font-size: 20px;
    color: white;
}
.camera__wrapper {
  position: relative;
  width: 80%;
  max-width: 680px;
  margin: 0 1rem 1rem;
  border-radius: 20px;
  border-style:double;
  /* background: rgb(40,44,52); */
}
.webcam:hover{
 cursor: pointer;
}
.webcam1{
  color: wheat;
}
.camera_wrapper2{
  position: relative;
  width: 80%;
  height: 520px;
  max-width: 680px;
  margin: 0 1rem 1rem;
  border-radius: 100px;
  background-color: rgb(238, 220, 220);
  background: rgb(55,65,81);
    background-size: cover;
   background-blend-mode: darken;
   color: white;
}
.camera_wrapper3:hover{
  cursor: pointer;
}
.camera_wrapper33{

  padding-top: 25%;
  padding-left: 30%;
  padding-right: 30%;
  
  /* height:10px; */
  }
  .camera_wrapper32{
    color: black;
  }
  .camera_wrapper32:hover{
    cursor: pointer;
  
    /* padding-top: 80%;
    padding-left: 80%;
    padding-right: 0px; */
  }
.camera_wrapper3{


  padding-left: 30%;
  padding-right: 30%;
  
  /* height:10px; */
  }
.camera__wrapper video {
  border: 3px solid #fff;
  border-radius: 100px;
}

.camera__button-container {
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.camera__button--snap {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  font-size: 20px;
  cursor: pointer;
}

.webcam-overlay {
  position: absolute;
  left: 0;
  top: 0;
}

.webcam-overlay--hidden {
  display: none;
}

.results__container {
  background-color: #fff;
  background: white;
  width: 100%;
  padding-top: 1rem;
  height:auto;
  display: flex;
  justify-content: center;
  color:white;
  border-radius: 100px;
}

.results__wrapper {
  padding: 1rem 0 0 1rem;
}

@media only screen and (min-width: 768px) {
  .camera__wrapper {
    width: 100%;
    margin: 0 1rem 1rem;
  }

  .results__wrapper {
    padding: 0;
  }
}

.App {
 background: white;
  background-size: cover;
  background-blend-mode: darken;
  min-height: 100vh;
  border: violet;
  border-width: 10px;
}

.App header {
  background:white;
}

.App__header {
  display: flex;
  max-width: 960px;
  margin: 0 auto;
  padding: 0.5rem;
  justify-content: space-between;
  align-items: center;
  height: 4em;
  background: white;
}

.App__switcher {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 22px;
}

.App__switcher-switch {
  margin: 0 0.25rem;
}

